@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Roboto&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Nunito", sans-serif;
}

.main {
  margin: 50px;
}

/* !Youtube Component */
.heading-p {
  text-align: center;
}

.container {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.youtube .images {
  width: 100%;
  position: relative;
}

.youtube .images img {
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  width: 100%;
}

.youtube .images .time {
  position: absolute;
  right: 5px;
  bottom: 13px;
  background: black;
  color: white;
  padding: 5px;
  border-radius: 5px;
  font-weight: 600;
  font-size: 12px;
}

.youtube p {
  margin: 0 !important;
}

.youtube .title {
  font-size: 18px;
  font-weight: 600;
}

.youtube .desc {
  font-size: 14px;
  color: grey;
}

/* !Youtube Component */
/* !Product Component */
.header {
  margin: 0px auto;
  width: 250px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .logo {
  width: 100px;
  margin-left: -15px;
}

.header img {
  width: 100%;
}

.header .troley {
  width: 30px;
  top: -10px;
  position: relative;
}

.header .troley .count {
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  color: white;
  background: green;
  border-radius: 50%;
  padding: 5px;
  text-align: center;
  width: 25px;
  height: 25px;
  top: 10px;
  left: -5px;
}

.product {
  margin: auto;
  width: 250px;
  padding: 10px;
  border-radius: 5px !important;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.6);
}

.product .images img {
  border-radius: 5px !important;
  box-shadow: 0 0 3px green, 0 0 5px rgba(0, 0, 0, 0.2);
  width: 100%;
}

.product p {
  margin: 0px;
  padding: 10px;
}

.product .price {
  margin-top: -10px;
  color: green;
  font-weight: bold;
}

.product .button-group {
  display: flex;
}

.product .button-group > * {
  background: #1641ff;
  border: none;
  color: white;
  padding: 8px;
  border-radius: 4px;
  margin: 0.5px;
  outline: none;
  font-weight: bold;
}

.product .button-group button {
  cursor: pointer;
  padding: 10px 15px !important;
}

.product .button-group input {
  text-align: center;
  flex: 2;
  color: black !important;
  border: 1px solid black !important;
  background: white !important;
  width: 50%;
}

/* !Product Component */
/* !BlogPost Component */
.container-blogpost {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 15px;
}

.form-add {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3), 3px 3px 5px #1641ff;
  grid-column: 1 / -1;
  padding: 10px;
  border-radius: 5px;
  margin: 30px auto;
  display: flex;
  flex-direction: column;
  width: 360px;
}

.form-add > label {
  font-weight: 600;
}

.form-add > * {
  margin: 5px;
}

.form-add input,
.form-add textarea,
.form-add button {
  font-family: "Nunito";
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  outline: none;
  padding: 10px;
  color: rgba(0, 0, 0, 0.5);
  transition: 0.3s ease-in;
}

.form-add button {
  cursor: pointer;
  background: none;
  border: 1px solid #1641ff !important;
  color: #1641ff;
}

.form-add button:hover {
  background: #1641ff;
  color: white;
}

.form-add button:active {
  box-shadow: 0 0 10px #1641ff !important;
}

.post {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3), 5px 5px 5px rgba(0, 0, 0, 0.2);
  padding: 5px;
  border-radius: 4px;
  position: relative;
  font-family: "Nunito", sans-serif;
  text-align: center;
}

.post .images img {
  border-radius: 4px;
  width: 100%;
  object-fit: cover;
}

.post p {
  margin: 0;
  padding: 5px;
}

.post .title {
  font-weight: 600 !important;
  font-size: 17px;
}

.post .desc {
  font-weight: 300;
  font-size: 15px;
  opacity: 0.5;
}

.post .buttonGroup {
  position: absolute;
  top: 0;
  left: 0;
}

.post .buttonGroup .remove,
.post .buttonGroup .update,
.post .buttonGroup .detail {
  border: none;
  outline: none;
  background: red;
  color: white;
  border-radius: 4px;
  text-align: center;
  padding: 12px 10px;
  cursor: pointer;
  text-transform: capitalize;
  margin: 1px;
  font-family: "Nunito", sans-serif;
}

.post .buttonGroup .update {
  background: black !important;
}

.post .buttonGroup .detail {
  background: green;
}

/* !BlogPost Component */
/* !Lifecycle Component */
.btn {
  display: flex;
  margin: 50px auto;
  background: lightskyblue;
  padding: 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 0.3);
  color: white;
  cursor: pointer;
}

/* !Lifecycle Component */
/* !Navbar */
nav {
  padding: 20px 50px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.4), 2px 2px 5px black;
}

nav h1 {
  text-transform: uppercase;
  font-family: "Bebas Neue", sans-serif;
  font-size: 2em;
}

nav ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 5px;
  list-style: none;
}

nav ul li {
  width: 100%;
  background: black;
  color: white;
  text-align: center;
  border-radius: 4px;
  transition: all 0.3s ease-in;
}

nav ul li a {
  padding: 8px 10px;
  display: block;
  color: white !important;
  font-family: "Roboto", sans-serif !important;
  text-decoration: none;
  font-weight: 600;
}

nav ul li:hover {
  background: transparent;
  box-shadow: 0 0 1px 1px black;
}

nav ul li:hover a {
  color: black !important;
}

@media (max-width: 576px) {
  nav ul li a {
    font-size: 12px;
  }
}

/* !Navbar */
.contentHooks {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 55vh;
}

.contentHooks p {
  margin: 10px;
}

.contentHooks button {
  padding: 8px 12px;
  cursor: pointer;
  background: black;
  color: white;
  border: none;
  outline: none;
  border-radius: 4px;
  transition: all 0.3s ease-in;
}

.contentHooks button:hover {
  background: none;
  color: black;
  box-shadow: 0 0 1px 1px black;
}

.contentTodo {
  text-align: center;
}

.contentTodo .headingTodo {
  font-size: 20px;
  font-weight: 600;
}

.contentTodo p,
.contentTodo .form {
  padding: 20px;
}

.contentTodo .form input,
.contentTodo button {
  padding: 10px 15px;
  margin-right: 3px;
  border-radius: 4px;
  outline: none;
  border: none;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

.contentTodo input {
  border: 1px solid black !important;
}

.contentTodo button {
  background: black;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease-in;
  font-family: "Roboto";
}

.contentTodo button:hover {
  background: none;
  box-shadow: 0 0 1px 1px black;
  color: black;
}

.contentTodo ul {
  list-style: none;
}

.contentTodo ul li {
  font-size: 18px;
}
